.KeyboardMenu .bubble{width:100% !important;max-width:27rem;border-radius:var(--border-radius-default-small)}.KeyboardMenu .content{display:flex;flex-direction:column;padding:.1875rem .625rem;max-height:80vh;overflow:auto}@media (max-width: 600px){.KeyboardMenu .content{max-height:75vh}}.KeyboardMenu .content .row{display:flex;flex-direction:row}.KeyboardMenu .content .row+.row{margin-top:.375rem}.KeyboardMenu .content .Button{flex:1;width:auto;height:auto;min-height:3.0625rem;border-radius:var(--border-radius-messages-small);border:2px solid var(--color-primary);background:var(--color-background);color:var(--color-primary);font-weight:500;text-transform:none}.KeyboardMenu .content .Button:hover{color:#fff;border-color:var(--color-primary-shade)}.KeyboardMenu .content .Button+.Button{margin-left:.375rem}
